const zoomBtn = document.querySelectorAll('.zoom-text') as NodeListOf<HTMLButtonElement>
const allImages = document.querySelectorAll('.img-container') as NodeListOf<HTMLDivElement>
const imageView = document.querySelector('.image-view') as HTMLDivElement
const nextBtn = document.getElementById('next-btn') as HTMLButtonElement
const prevBtn = document.getElementById('prev-btn') as HTMLButtonElement
const imageBox = document.querySelector('.image-box') as HTMLDivElement

let currentImageIdx = 0

imageView.addEventListener('click', () =>
{
    imageView.style.display = "none"
    imageBox.style.display = "none"
})

zoomBtn.forEach((btn, index) =>
{
    btn.addEventListener('click', () =>
    {
        imageView.style.display = "block"
        imageBox.style.display = "block"
        ++currentImageIdx
        currentImageDisplay(currentImageIdx)
    })
})

const currentImageDisplay = (position: number) =>
{
    imageBox.style.background = `url(images/img${currentImageIdx}.jpg) center/cover no-repeat`
}

prevBtn.addEventListener('click', () =>
{
    --currentImageIdx
    if (currentImageIdx === 0)
    {
        currentImageIdx = allImages.length
    }
    currentImageDisplay(currentImageIdx)
})

nextBtn.addEventListener('click', () =>
{
    ++currentImageIdx
    if (currentImageIdx === 5)
    {
        currentImageIdx = 1
    }
    currentImageDisplay(currentImageIdx)
})
